import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('../views/Index.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About')
  },
  {
    path: '/user/email/verify/:code',
    name: 'UserEmailVerify',
    props: true,
    component: () => import('../views/UserEmailVerify')
  },
  {
    path: '/user/email/again',
    name: 'UserEmailAgain',
    props: true,
    component: () => import('../views/UserEmailAgain')
  },
  {
    path: '/*',
    name: 'pageNotFound',
    component: () => import('../views/error/404')
  }
]

const router = new VueRouter({
  // TODO 2020/05/12 github page无法使用 history 模式
  //mode: 'history',
  routes
})

export default router
