import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShowAppInfo: false,
    isShowBoard: false,
    updateContent: '',
    name: '',
    url: '',
    bulletinBoardContent: ''
  },
  mutations: {
    setAppInfo(state, data){
      state.isShowAppInfo = true;
      state.updateContent = data.updateContent.replace('\n','<br/>');
      state.name = data.versionName;
      state.url = data.downloadLink;
    },
    setBulletinBoard(state, data){
      state.isShowBoard = true;
      state.bulletinBoardContent = data;
    }
  },
  actions: {
    getAppInfo(){
      axios.get('/updateCheck')
        .then(resp=>{
          if (resp.data.status === 200) {
            this.commit('setAppInfo', resp.data.result)
          }
        })
    },
    getBulletinBoard() {
      axios.get('/bulletinBoard')
        .then(resp => {
          if (resp.data.status === 200) {
            // 获取数据成功
            this.commit('setBulletinBoard', resp.data.result.content)
          }
        })
    }
  }
})
