<template>
  <div id="app">
    <Header :style="menuStyle"/>
    <!--路由容器-->
    <router-view :style="{minHeight:minHeight + 'px'}"/>

    <Footer :style="footerStyle"/>
  </div>
</template>

<script>
  import Header from './components/Header'
  import Footer from "./components/Footer";

  export default {
    data() {
      return {
        minHeight: 0,
        headerFooter: 180,
        menuStyle: {
          height: '60px'
        },
        footerStyle: {
          height: '120px'
        }
      }
    },
    components: {
      Header,
      Footer
    },
    mounted() {
      window.screenWidth = document.body.clientWidth;
      if (window.screenWidth < 700) {
        this.footerStyle.height = '120px';
      }

      // 动态设置内容高度 让footer始终居底   header+footer的高度是180
      this.minHeight = document.documentElement.clientHeight - this.headerFooter;
      // 监听浏览器窗口变化
      window.onresize = () => {
        this.minHeight = document.documentElement.clientHeight - this.headerFooter
      }

      if (window.screenWidth < 700) {
        this.menuStyle.height = '60px';
      }

      // 获取标签集合
      this.$store.dispatch('getAppInfo');
      this.$store.dispatch('getBulletinBoard');
    }
  }
</script>

<style>
  body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  }

  /* 更好看的侧边栏 */
  ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #eee;
    border-radius: 3px;
  }

  .text-color-amber {
